import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { Watchlist } from "@rp/common/api-types"
import { ActionsMenu } from "@rp/react/web/common/base-components"
import { WATCHLISTS_ROUTE } from "@rp/react/web/router"
import { useWatchlistsActions } from "@rp/react/web/watchlists"

type Props = {
  watchlist: Watchlist
  editable: boolean
}
const PortfolioActionsMenu = ({ watchlist, editable }: Props) => {
  const navigate = useNavigate()
  const [open, setOpen] = useState(false)
  const { copy, rename, remove } = useWatchlistsActions({
    removeParams: { onSuccess: () => navigate(WATCHLISTS_ROUTE) },
  })

  return (
    <ActionsMenu
      open={open}
      setOpen={setOpen}
      actions={editable ? [copy, rename, remove] : [copy]}
      item={watchlist}
      sxIconButton={{ color: "grey.300" }}
      openColor="grey.300"
    />
  )
}
export { PortfolioActionsMenu }
